import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CommentsUnapprovedComponent } from './comments-unapproved.component';

const routes: Routes = [
  { path: '', component: CommentsUnapprovedComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CommentsUnapprovedRoutingModule { }



